import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { UserRegistrationComponent } from '@account';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { ENDPOINTS, ERROR_MESSAGES } from '@shared';
import { Company, UserAccount } from '@models';
import { NgxSpinnerService } from 'ngx-spinner';
import { UserLogin } from 'app/core/models/user-login.model';
import jwt_decode from 'jwt-decode';
import { TokenService } from 'app/core/services/authorization/token.service';
import { ForgotPassowrdComponent } from './forgot-passowrd/forgot-passowrd.component';
import { ImageObject } from 'app/core/models/image-object.model';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  userProfile = new UserAccount();
  company = new Company();
  userLogin = new UserLogin();
  imageObject:Array<object>=[];

  // username: string;
  // password: string;
  hide = true;
  componentWidth = 0;
  loginImageList = [];
  image = [];

  barangay = '';
  cityMunicipality = '';
  province = '';
  region = '';
  fullAddress = '';

  constructor(
    private router: Router,
    private dialog: MatDialog,
    private apiService: ApiService,
    private dataStorage: DataStorageService,
    private tokenService: TokenService,
    private sweetAlertService: SweetAlertService,
    private spinner: NgxSpinnerService
  ) { }

  ngOnInit(): void {
    this.getLoginImage();
    this.dataStorage.signOut();
  }

  login(): void {

    if (this.userLogin.username && this.userLogin.password) {
      this.spinner.show();
      this.userLogin.username = this.userLogin.username.toLowerCase();

      this.apiService.save(`${ENDPOINTS.authentication}/login`, this.userLogin).subscribe(
        (data: any) => {
          this.apiService.findByParam(ENDPOINTS.userLogin, this.userLogin.username).subscribe(
            (userDetails: any) => {
              const responseData = data?.responseData?.data;
              this.tokenService.setUserLoginToken(`${responseData?.tokenType} ${responseData?.accessToken}`);
              const decodedToken = jwt_decode(responseData?.accessToken);
              this.tokenService.setUserLoginData(decodedToken);

              this.dataStorage.saveUserRole(userDetails.responseData.data.userRole.name);
              this.dataStorage.saveUserAccount(userDetails.responseData.data);
              this.dataStorage.saveUserFirstname(userDetails.responseData.data.firstName);
              this.getUserProfile(userDetails.responseData.data.id);
              this.router.navigateByUrl('/asc/page/announcement');
              this.hideSpinner();
            }, (err: any) => {
              console.log(err);
              this.sweetAlertService.error(err);
              this.hideSpinner();
            });
        }, (err: any) => {
          if (err.error.message.includes('not verified')) {
            err.error.message = 'Your account has not yet been verified. Please check your email.';
            this.sweetAlertService.error(err);
          } else if (err.error.message === 'Username or Password is incorrect') {
            this.apiService.findAll(ENDPOINTS.existsByUsername + this.userLogin.username).subscribe((res: any) => {
              if (res === false) {
                err.error.message = 'User does not exist';
                this.sweetAlertService.error(err);
              } else {
                err.error.message = 'Password is incorrect';
                this.sweetAlertService.error(err);
              }
            });
          } else if (err.statusText === 'Unknown Error') {
            err.error.message = ERROR_MESSAGES.under_maintenance;
            this.sweetAlertService.error(err);
          } else {
            err.error.message = 'Password is incorrect';
            this.sweetAlertService.error(err);
          }
          this.hideSpinner();
        }, () => {
          this.hideSpinner();
        }
      );
    } else if (!this.userLogin.username) {
      this.sweetAlertService.customErrorMessage('Please enter username.');
    } else if (!this.userLogin.password) {
      this.sweetAlertService.customErrorMessage('Please enter password.');
    }
  }

  /**
   * @author Julius Papa
   * @param evt Event from html field
   * @summary What will be triggered on press of "Enter" key.
   * @description If in "username" field, it will go to password field.
   * @description If in "password" field, it will trigger login function.
   */
  loginByField(evt) {
    if (evt.key) {
      if (evt.key.toLowerCase() === 'enter') {
        switch (evt.target.id.toLowerCase()) {
          case 'username':
            document.getElementById('password').focus();
            break;
          case 'password':
            this.login();
            break;
          default:
            break;
        }
      }
    }
  }

  openRegistrationModal() {
    // const dialogRef = this.dialog.open(UserRegistrationComponent, {
    //   width: '35%',
    //   height: '55%',
    //   data: {status: 'Register'}
    // });
    const dialogRef = this.dialog.open(UserRegistrationComponent, {
      width: '80%',
      height: '95%',
      panelClass: 'custom-dialog',
      data: {
        status: 'Register',
        isRegister: true
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  getUserProfile(accountId): void {
    this.apiService.findById(ENDPOINTS.userAccount, accountId).subscribe(
      (res: any) => {
        this.userProfile = res.responseData.data;
        this.dataStorage.saveUserAccount(this.userProfile);
        this.apiService.findById(ENDPOINTS.company, this.userProfile.company.id).subscribe(
          (res1: any) => {
            this.company = res1.responseData.data;
            this.dataStorage.saveUserCompany(this.company);
            // this.addressService.getBarangay(this.company.barangay.barangayId).subscribe(
            //   (res: any) => {
            //     this.barangay = res.responseData.data.barangayName;
            //     this.cityMunicipality = res.responseData.data.cityMunicipalityDto.cityMunicipalityName;
            //     this.province = res.responseData.data.cityMunicipalityDto.provinceDto.provinceName;
            //     this.region = res.responseData.data.cityMunicipalityDto.provinceDto.regionDto.regionName;
            //     this.fullAddress = `${this.barangay}, ${this.cityMunicipality}, ${this.province}, ${this.region}`;
            //     this.dataStorage.saveUserAddress(this.fullAddress);
            //   });
          });
      });
  }

  hideSpinner() {
    setTimeout(() => {
      this.spinner.hide();
    }, 1);
  }

  forgotPassword() {
    const dialogRef = this.dialog.open(ForgotPassowrdComponent, {
      panelClass: 'custom-modalbox'
    });
  }

  getLoginImage = async () => {
    this.apiService.findAll(ENDPOINTS.loginImage).subscribe(
      (res: any) => {
        let list = [];
        res.responseData.data.forEach(tl => {
          var obj = {
            path:tl.fileURL
          }
          this.imageObject.push(obj);
        })
      }
    )
  }

}


