import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';

@Component({
  selector: 'app-forgot-passowrd',
  templateUrl: './forgot-passowrd.component.html',
  styleUrls: ['./forgot-passowrd.component.scss']
})
export class ForgotPassowrdComponent implements OnInit {

  userName: string;

  constructor(
    private apiService: ApiService,
    private sweetAlertService: SweetAlertService,
    public dialogRef: MatDialogRef<ForgotPassowrdComponent>,
  ) { }

  ngOnInit(): void {
  }

  resetPassword() {
    if (this.userName) {
      console.log(this.userName);
      this.apiService.save(`${ENDPOINTS.forgotPassword}/${this.userName}`, null).subscribe(
        res => {
          console.log(res);
          this.sweetAlertService.success(res, 5);
          // this.sweetAlertService.customSuccessMessage("Your password has been changed successfully", 5);
          // this.sweetAlertService.success("Your password has been changed successfully", 5);
          this.dialogRef.close();
        }, err => {
          console.log(err);
          this.sweetAlertService.error(err);
        }
      );
    } else {
      this.sweetAlertService.customErrorMessage('Username field is blank. Please provide a valid username value.');
    }
  }

  closeModal() {
    this.dialogRef.close();
  }

}
